<template>
  <header
    class="header"
    :class="{
      'tablet-header': isTablet
    }"
  >
    <div class="header__logo">
      <Icon
        name="logo"
        :width="isDesktop ? '69px' : '53px'"
        :height="isDesktop ? '68px' : '52px'"
      />
      <Icon
        name="logo-title"
        :width="isDesktop ? '141px' : '109px'"
        :height="isDesktop ? '25px' : '19px'"
      />
    </div>
    <div class="header__menu">
      <Button
        v-for="item in routes"
        :key="item.name"
        :type="activeRoute === item.name ? 'primary' : 'white'"
        class="header__menu__link"
        @click="linkClick(item)"
      >
        <Text weight="800">{{ item.title }}</Text>
      </Button>
      <div class="header__menu__divider" />
      <Button
        class="header__menu__contact"
        @click="onContactSupport"
        @mouseover="toggleShowEmail(true)"
        @mouseleave="toggleShowEmail(false)"
      >
        <Text weight="800">{{ isShowEmail ? contactEmail : 'Contact' }}</Text>
      </Button>
    </div>
  </header>
</template>

<script>
import Icon from '@/components/UIKit/Icon'
import Button from '@/components/UIKit/Button'
import Text from '@/components/UIKit/Text/Text'
import { useHeader } from '@/components/Header/useHeader'
import { useBreakpointsMedia } from '@/plugins/breakpoints'

export default {
  name: 'Header',
  components: {
    Text,
    Button,
    Icon
  },
  setup () {
    const {
      routes,
      breakpoints,
      isShowEmail,
      activeRoute,
      contactEmail,
      linkClick,
      toggleShowEmail,
      onContactSupport
    } = useHeader()
    const { isDesktop, isTablet } = useBreakpointsMedia()

    return {
      routes,
      isTablet,
      isDesktop,
      breakpoints,
      isShowEmail,
      activeRoute,
      contactEmail,
      linkClick,
      toggleShowEmail,
      onContactSupport
    }
  }
}
</script>

